<template>
  <div class="container">
    <!-- <div class="row">
          <div class="col-md-12 my-3">
              <h2>Room</h2>
              <input v-model="roomId">
          </div>
      </div> -->

    <div class="row">
      <div class="col-md-12">
        <div class="">
          <vue-webrtc
            ref="webrtc"
            style="display: none"
            width="100%"
            :roomId="roomId"
            :enableLogs="true"
            :enableVideo="false"
            v-on:joined-room="logEvent"
            v-on:left-room="logEvent"
            v-on:opened-room="joined"
            v-on:share-started="logEvent"
            v-on:share-stopped="logEvent"
            @error="onError"
          />
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-md-12 center">
        <div
          v-show="call"
          style="font-size: 26px"
        >
          {{timer}}
        </div>
      </div>
    </div>

    <div class="row my-4">
      <div
        class="col-md-12 center"
        style="font-size: 26px"
      >
        <div v-show="call">
          Talking to customer service...
          <br />
          与服务人员通话中...
        </div>
        <div v-show="!call">
          Click "Start Call" to contact customer service
          <br />
          点击"开始通话"与客服联系
        </div>
      </div>
    </div>

    <div class="row footer">
      <div
        class="col-md-4"
        style="font-size: 26px"
      >
        <img
          class="btn"
          :src="loudspeakerOn"
          style="width: 100%"
        />
        Speaker
        <br />
        免提
      </div>
      <div
        class="col-md-4"
        style="font-size: 26px"
      >
        <img
          :src="callOff"
          class="btn"
          style="width: 100%"
          @click="onLeave"
        />
        End Call
        <br />
        结束通话
      </div>
      <div
        class="col-md-4"
        style="font-size: 26px"
      >
        <img
          :src="callOn"
          class="btn"
          style="width: 100%"
          @click="onJoin"
        />
        Start Call
        <br />
        开始通话
        <!-- <button type="button" class="btn btn-primary" @click="onCapture">Capture Photo</button>
                      <button type="button" class="btn btn-primary" @click="onShareScreen">Share Screen</button> -->
      </div>
    </div>
    <!-- <div class="row">
          <div class="col-md-12">
              <h2>Captured Image</h2>
              <figure class="figure">
                  <img :src="img" class="img-responsive" />
              </figure>
          </div>
      </div> -->
  </div>
</template>

<script>
import { VueWebRTC } from 'vue-webrtc';
import callOn from '../assets/call-on.png';
import callOff from '../assets/call-off.png';
import loudspeakerOn from '../assets/loudspeaker-on.png';
import loudspeakerOff from '../assets/loudspeaker-off.png';

export default {
  name: 'demo-component',
  components: {
    'vue-webrtc': VueWebRTC
  },
  data() {
    return {
      roomId: 'customer-service-001',
      img: null,
      call: false,
      callOn,
      callOff,
      loudspeakerOn,
      loudspeakerOff,
      time: 0,
      timerID: null
    };
  },
  mounted: function () {},
  computed: {
    timer() {
      let hours = Math.floor(this.time / 3600);
      let minutes = Math.floor((this.time - hours * 3600) / 60);
      let seconds = this.time - hours * 3600 - minutes * 60;

      if (hours < 10) {
        hours = '0' + hours;
      }
      if (minutes < 10) {
        minutes = '0' + minutes;
      }
      if (seconds < 10) {
        seconds = '0' + seconds;
      }

      return `${hours}:${minutes}:${seconds}`;
    }
  },
  watch: {},
  methods: {
    onCapture() {
      this.img = this.$refs.webrtc.capture();
    },
    onJoin(e) {
      console.log(e);
      this.$refs.webrtc.join();
    },
    onLeave(e) {
      console.log(e);
      this.$refs.webrtc.leave();
      this.call = false;
      clearInterval(this.timerID);
      this.timerID = null;
      this.time = 0;
    },
    onShareScreen() {
      this.img = this.$refs.webrtc.shareScreen();
    },
    onError(error, stream) {
      console.log('On Error Event', error, stream);
    },
    joined(e) {
      console.log(e);
      this.call = true;
      if (!this.timerID) {
        this.timerID = setInterval(() => {
          this.time++;
        }, 1000);
      }
    },
    left(e) {
      console.log(e);
    },
    logEvent(event) {
      console.log('Event : ', event);
    }
  }
};
</script>

<style>
.footer {
  position: absolute;
  bottom: 0;
  left: 0;
}
</style>